<template>
    <div class="main-page">
        <svg version="1.1" id="Layer_1" x="0px" y="0px" width="24px" height="30px" viewBox="0 0 24 30" style="enable-background:new 0 0 50 50;" xml:space="preserve">
            <rect x="0" y="13" width="4" height="5" :fill="$config.theme">
                <animate attributeName="height" attributeType="XML" values="5;21;5" begin="0s" dur="0.6s" repeatCount="indefinite" />
                <animate attributeName="y" attributeType="XML" values="13; 5; 13" begin="0s" dur="0.6s" repeatCount="indefinite" />
            </rect>
            <rect x="10" y="13" width="4" height="5" :fill="$config.theme">
                <animate attributeName="height" attributeType="XML" values="5;21;5" begin="0.15s" dur="0.6s" repeatCount="indefinite" />
                <animate attributeName="y" attributeType="XML" values="13; 5; 13" begin="0.15s" dur="0.6s" repeatCount="indefinite" />
            </rect>
            <rect x="20" y="13" width="4" height="5" :fill="$config.theme">
                <animate attributeName="height" attributeType="XML" values="5;21;5" begin="0.3s" dur="0.6s" repeatCount="indefinite" />
                <animate attributeName="y" attributeType="XML" values="13; 5; 13" begin="0.3s" dur="0.6s" repeatCount="indefinite" />
            </rect>
        </svg>
    </div>
</template>

<script>
import { a } from "../../../public/static/js/qing"
import login from "./login.js"
export default {
    name: "Loading",
    data() {
        return {
            uJson: {},
            tcode: {}
        }
    },
    mixins: [login],
    methods: {
        getToken() {
            this._api.getToken({
                tcode: this.uJson.tcode,
                code: this.uJson.code,
                ticket: this.uJson.ticket,
                client_id: "client",
                client_secret: "secret",
                grant_type: "password",
                username: "udynamic" + Math.random().toFixed(5) * 100000,
                password: Math.random()
            }).then(res => {
                if (res) {
                    if (res.errcode == 1) {
                        setTimeout(() => {
                            this.$router.push("/Login")
                        }, 3000)
                        return
                    }
                    this.$u.setItem("token", res.access_token)
                    this.loginInit(res.access_token)
                }
            })
        },
        sso() {
            // 如果有上次登录成功的账号密码 则 直接进入
            if (navigator.userAgent.toLowerCase() == "micromessenger") {
                let up = localStorage.getItem("up")
                if (up) {
                    let obj = JSON.parse(up)
                    this._api.getToken(obj).then(res => {
                        if (res) {
                            if (res.errcode == 1) {
                                setTimeout(() => {
                                    this.$router.push("/Login")
                                }, 3000)
                                return
                            }
                            this.$u.setItem("token", res.access_token)
                            this.loginInit(res.access_token)
                        }
                    })
                    return
                }
            }

            // 解析 href
            let href = window.location.href
            this.uJson = this.$u.urlToJson(href)

            if (this.uJson.sign == "qsms") {
                this.uJson.toUrl = this.uJson.toUrl.replace(/%2F/g, '/')

                qing.call('getPersonInfo', {
                    'success': (result) => {
                        this._api.getToken({
                            tcode: "5653C942AA236456EEE3386EC7C9175B",
                            ticket: null,
                            openId: result.data.openId,
                            client_id: "client",
                            client_secret: "secret",
                            grant_type: "password",
                            username: "udynamic" + Math.random().toFixed(5) * 100000,
                            password: Math.random()
                        }).then(res => {
                            if (res) {
                                if (res.errcode == 1) {
                                    this.$u.msg("登录失败！", "/static/img/warning.png")
                                    setTimeout(() => {
                                        this.$router.push("/Login")
                                    }, 3000)
                                    return
                                }
                                this.$u.setItem("token", res.access_token)
                                this.uJson.toUrl = this.uJson.toUrl.replace(/%2F/g, '/')
                                this.$router.push(`${this.uJson.toUrl}?QuestionId=${this.uJson.QuestionId}&OptionId=${this.uJson.OptionId}&name=${result.data.name}&QuestionnaireId=${this.uJson.QuestionnaireId}`)
                            }
                        })
                    }
                })
                return
            }

            if (!this.uJson.tcode) {
                if (this.uJson.state) {
                    this.uJson.tcode = this.uJson.state
                } else {
                    // 云之家
                    this.uJson.tcode = this.$config.tcode
                }
                this.getToken()
            } else {
                this.getToken()
            }
        },
        qywx() {

        },
        // 去登录页
        toLogin() {
            this.$router.push('/Login')
        },
        // 去首页
        toHome() {
            this.$router.push('/Home')
        }
    },
    mounted() {
        this.$u.clear()
        this.sso()
    }
}
</script>

<style lang="scss" scoped>
.main-page {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}
</style>